import { PosBaseInfo } from '@/types'
import { getPoolDirection } from '@/utils/pool'
import { useGetToken } from '@cetus/hooks/src/useToken'
import { BurnUtils } from '@cetusprotocol/cetus-burn-sdk'
import { FramsUtils } from '@cetusprotocol/cetus-periphery-sdk'
import { buildPosition, extractStructTagFromType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import usePosHelper from './usePosHelper'

export default function useWrapPosData() {
  const { buildPositionType, buildFarmsPositionType, buildBurnPositionType } = usePosHelper()
  const { getTokenInfo } = useGetToken()

  const getPosTokenData = (coinTypeA: string, coinTypeB: string): any => {
    const isReverse = !getPoolDirection(coinTypeA, coinTypeB)

    const tokenA = getTokenInfo(coinTypeA)
    const tokenB = getTokenInfo(coinTypeB)
    const displayTokenA = !isReverse ? tokenA : tokenB
    const displayTokenB = !isReverse ? tokenB : tokenA

    return {
      isReverse,
      tokenA,
      tokenB,
      displayTokenA,
      displayTokenB
    }
  }

  // Clmm仓位包装
  const wrapClmmPosBaseInfo = (data: any): PosBaseInfo => {
    const pos = buildPosition(data)
    const coinTypeA = extractStructTagFromType(pos.coin_type_a).full_address
    const coinTypeB = extractStructTagFromType(pos.coin_type_b).full_address
    const { isReverse, tokenA, tokenB, displayTokenA, displayTokenB } = getPosTokenData(coinTypeA, coinTypeB)

    const version = data?.data?.version || '0'

    return {
      clmmPool: pos.pool,
      posId: pos.pos_object_id,
      id: pos.pos_object_id,
      liquidity: pos.liquidity,
      index: pos.index,
      lowerTick: pos.tick_lower_index,
      upperTick: pos.tick_upper_index,
      owner: pos.owner,
      coinTypeA,
      coinTypeB,
      posType: 'clmm',
      tokenName: '',
      tokenA,
      tokenB,
      displayTokenA,
      displayTokenB,
      isReverse,
      version
    }
  }

  // Farms仓位包装
  const wrapFarmsPosBaseInfo = (data: any): PosBaseInfo => {
    const pos: any = FramsUtils.buildFramsPositionNFT(data)

    const version = data?.data?.version || '0'
    const coinTypeA = extractStructTagFromType(pos.coinTypeA).full_address
    const coinTypeB = extractStructTagFromType(pos.coinTypeB).full_address

    const { isReverse, tokenA, tokenB, displayTokenA, displayTokenB } = getPosTokenData(coinTypeA, coinTypeB)

    return {
      clmmPool: pos.clmm_pool_id,
      farmsPool: pos.pool_id,
      posId: pos.clmm_position_id,
      id: pos.id,
      liquidity: pos.liquidity,
      index: pos.name.split('-')[1],
      lowerTick: pos.tick_lower_index,
      upperTick: pos.tick_upper_index,
      owner: '',
      coinTypeA,
      coinTypeB,
      posType: 'farms',
      tokenName: pos.name,
      isReverse,
      tokenA,
      tokenB,
      displayTokenA,
      displayTokenB,
      version
    }
  }

  // Burn仓位包装
  const wrapBurnPosBaseInfo = (data: any): PosBaseInfo => {
    const pos: any = BurnUtils.buildBurnPositionNFT(data?.data?.content?.fields)
    const version = data?.data?.version || '0'
    const coinTypeA = extractStructTagFromType(pos.coinTypeA).full_address
    const coinTypeB = extractStructTagFromType(pos.coinTypeB).full_address

    const { isReverse, tokenA, tokenB, displayTokenA, displayTokenB } = getPosTokenData(coinTypeA, coinTypeB)

    return {
      clmmPool: pos.clmm_pool_id,
      posId: pos.clmm_position_id,
      liquidity: pos.liquidity,
      index: pos.index,
      id: pos.id,
      lowerTick: pos.tick_lower_index,
      upperTick: pos.tick_upper_index,
      owner: '',
      coinTypeA,
      coinTypeB,
      posType: 'burn',
      tokenName: pos.name,
      isReverse,
      tokenA,
      tokenB,
      displayTokenA,
      displayTokenB,
      version
    }
  }

  const buildPosBaseInfo = (item: any): PosBaseInfo | null => {
    const type = extractStructTagFromType(item.data.type)

    if (type.full_address === buildPositionType) {
      return wrapClmmPosBaseInfo(item)
    }

    if (type.full_address === buildFarmsPositionType) {
      return wrapFarmsPosBaseInfo(item)
    }

    if (type.full_address === buildBurnPositionType) {
      return wrapBurnPosBaseInfo(item)
    }

    return null
  }

  return {
    wrapClmmPosBaseInfo,
    wrapBurnPosBaseInfo,
    wrapFarmsPosBaseInfo,
    buildPosBaseInfo
  }
}
