import { PosBaseInfo } from '@/types'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { isAvailableObject } from '@cetus/utils'
import { useMemo } from 'react'

export default function usePosHelper() {
  const getClmmPosName = (posIndex: number, position_index: string | number) => {
    if (position_index || position_index == 0) {
      return `Cetus LP | Pool${position_index}-${posIndex}`
    }
    return undefined
  }

  const buildPositionType = useMemo(() => {
    return `${envConfigs.clmm_sdk_options.clmm_pool.package_id}::position::Position`
  }, [envConfigs])

  const buildFarmsPositionType = useMemo(() => {
    return `${envConfigs.periphery_sdk_options.frams.package_id}::pool::WrappedPositionNFT`
  }, [envConfigs])

  const buildBurnPositionType = useMemo(() => {
    return `${envConfigs.burn_sdk_options.burn.package_id}::lp_burn::CetusLPBurnProof`
  }, [envConfigs])

  const getTokenALock = (currentPosBaseInfo: PosBaseInfo, curPosContractPoolInfo: any) => {
    if (isAvailableObject(currentPosBaseInfo) && isAvailableObject(curPosContractPoolInfo)) {
      const { lowerTick, upperTick } = currentPosBaseInfo
      const currentTickIndex = curPosContractPoolInfo?.current_tick_index
      if (currentTickIndex !== undefined && lowerTick !== undefined && upperTick !== undefined) {
        if (currentTickIndex >= lowerTick && currentTickIndex < upperTick) {
          return false
        } else if (currentTickIndex >= upperTick) {
          return true
        } else if (currentTickIndex < lowerTick) {
          return false
        } else {
          return true
        }
      }
    }
    return false
  }

  const getTokenBLock = (currentPosBaseInfo: PosBaseInfo, curPosContractPoolInfo: any) => {
    if (isAvailableObject(currentPosBaseInfo) && isAvailableObject(curPosContractPoolInfo)) {
      const { lowerTick, upperTick } = currentPosBaseInfo
      const currentTickIndex = curPosContractPoolInfo?.current_tick_index
      if (currentTickIndex !== undefined && lowerTick !== undefined && upperTick !== undefined) {
        if (currentTickIndex >= lowerTick && currentTickIndex < upperTick) {
          return false
        } else if (currentTickIndex >= upperTick) {
          return false
        } else if (currentTickIndex < lowerTick) {
          return true
        } else {
          return true
        }
      }
    }
    return false
  }

  return {
    getClmmPosName,
    buildPositionType,
    buildFarmsPositionType,
    buildBurnPositionType,
    getTokenALock,
    getTokenBLock
  }
}
